* {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}

.all-tables {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 50px;
  justify-content: center;
  align-items: center;
}

[class^="table"] {
  width: 35%;
  height: 20vh;
  border: 1px solid gray;
  margin: 10px;
  padding: 10px;
  display: flex;
  position: relative;
}

.person {
  width: 20%;
  display: flex;
  flex-direction: column;
  margin-right: 5px;
}

.complete {
  width: 80px;
  height: 25px;
}

.price {
  position: absolute;
  right: 0;
  bottom: 0;
}

.notification {
  border: 1px solid red;
  color: red;
  padding: 5px;
  width: 98%;
  margin-top: 2px;
}

.house-control {
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.house-control>* {
  margin-bottom: 3px;
}

.console {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 0 auto;
  font-style: italic;
}

.profit {
  font-weight: bold;
}

@media (max-width: 1060px) {
  .all-tables {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  [class^="table"] {
    width: 90%;
  }
}